import React from 'react';
import Signout from './Signout';
import './FacultyDashboard.css';
import Avatar from '@material-ui/core/Avatar';

function FacultyDashboard(){
    return (
        <div>
            <div className="navbar">
                <h2>Faculty Dashboard</h2>
                <Signout className="google-logout"/>
            </div>
            <div className="content">
                <div className="left-content">
                    <div className="left-content-heading">
                        <Avatar alt="Akash Srivastava" src="https://cdn-icons-png.flaticon.com/512/201/201818.png" />
                        <button><h4>Faculty Details</h4></button>
                    </div>
                    <div className="faculty-details">
                        <p>Name: Akash Srivastava</p>
                        <p>Semester: 7</p>
                        <p>Email: 500071431@stu.upes.ac.in</p>
                        
                    </div>
                </div>

                <div className="middle-content">
                    <div className="middle-content-heading">
                        <Avatar alt="Akash Srivastava" src="https://icons-for-free.com/iconfiles/png/512/cloud+upload+file+storage+upload+icon-1320190558968694328.png" />
                        <button><h4>Post assignment</h4></button>
                    </div>
                    <div className="assignment-details">
                        <form>
                            <label for="date">Deadline - : </label>
                            <input type="date" name="date"></input>
                            
                            <input type="file" id="myFile" name="filename"></input>
                            <br />
                            <br />
                            <input type="submit" className="submit-button"></input>
                        </form>
                        
                    </div>
                </div>
                <div className="right-content">
                    <div className="right-content-heading">
                        <Avatar alt="Akash Srivastava" src="https://www.nicepng.com/png/detail/10-101646_borrow-library-books-book-stack-books-icon.png" />
                        <button><h4>Assignments</h4></button>
                    </div>
                    <div className="task-details">
                        <p>List of assignments -:</p>
                        
                        
                    </div>
                </div>
            </div>
        </div>
    );
}

export default FacultyDashboard;