import React from 'react';
import './Homeboard.css';
import {Link} from 'react-router-dom';
function Homeboard(){
    return (
        <div className="homeboard">
            <div className="homeboard-header">
                <h1 className="homeboard-welcome">Welcome</h1>
            </div>
            <div className="homeboard-content">
                <Link to="/StudentLogin">
                    <button className="homeboard-button-student">Student</button>
                </Link>
                <Link to="/FacultyLogin">
                    <button className="homeboard-button-faculty">Faculty</button>
                </Link>
            </div>
        </div>
    );
}

export default Homeboard;