import React from 'react';
import "./FacultyLogin.css";
import {Link} from 'react-router-dom';

function FacultyLogin(){
    return(
        <div className="faculty-login">
            <h1>Faculty Login</h1>
            <form method="post" className="faculty-login-form">
                <div className="email">
                    <label for="email">Email:</label>
                    &nbsp;&nbsp;&nbsp;&nbsp;
                    <input type="email" name="email" id="faculty-email" className="faculty-email"/>
                </div>
                <div className="password">
                    <label for="password">Password:</label>
                    <input type="password" id="faculty-password" name="password" className="faculty-password"/>
                </div>
                <Link to="/faculty-dashboard">
                    <input type="submit" value="Submit" class="faculty-submit-button"/>
                </Link>
            </form>
        </div>
    );
}
export default FacultyLogin;