import React from 'react';
import { GoogleLogout } from  'react-google-login';

const clientId = '931411910180-cctjs62mbu2esfgvh229bgl66q2a3inm.apps.googleusercontent.com';

function Signout(){
    const onSuccess = (res) => {
        alert('Signout made successfully');
    };

    return (
        <div>
            <GoogleLogout
            clientId={'931411910180-cctjs62mbu2esfgvh229bgl66q2a3inm.apps.googleusercontent.com'}
            buttonText="Logout"
            onLogoutSuccess={onSuccess}
            />
        </div>
    );
}

export default Signout;