import React from 'react';
import { Link } from 'react-router-dom';
import LoginPage from './LoginPage';
import Signin from './Signin';
import Signout from './Signout';
import './StudentLogin.css';

function StudentLogin(){
    return(
        <div className="student-login">
            <h1>Student Login</h1>
            <form method="post" className="student-login-form">
                <div className="email">
                    <label for="email">Email:</label>
                    &nbsp;&nbsp;&nbsp;&nbsp;
                    <input type="email" name="email" id="faculty-email" className="student-email"/>
                </div>
                <div className="password">
                    <label for="password">Password:</label>
                    <input type="password" id="student-password" name="password" className="student-password"/>
                </div>
                <Link to="student-dashboard">
                    <input type="submit" value="Submit" class="student-submit-button"/>
                </Link>
            </form>
            <br />
            <Signin className="google-login"/>
        </div>
    );
}
export default StudentLogin;